@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
:root{
  --primary_color:#0d0d26;
  --white:#fff;
  --black:#000;
  --font_weight_regular:400;
  --font_weight_medium:500;
  --font_weight_semibold:600;
  --font_weight_bold:700;
}
*{
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;

}
body{
  box-sizing: border-box;
}


/* navbar */

.navbar{
  z-index: 100;
  position: fixed;
  width: 100%;
  padding: 1.5% 2%;
  transition: .5s;
}
.navbar.sticky{
  background-color: #0d0d26;
  background-color: var(--primary_color);
  color: #0d0d26;
  color: var(--primary_color);
}
.navbar-nav{
  margin: 0 20%;
}
.navbar-brand{
  color: #fff;
  color: var(--white);
  font-size: 2rem;
  font-family: 'Lobster', cursive;
}
.navbar-brand:hover{
  color: #fff;
  color: var(--white);
}
.nav-item{
  display: block;
  margin-right: 4%;
}
.nav-link{
  color: #fff;
  color: var(--white);
  font-size: 1.1rem;
  font-weight: 400;
  font-weight: var(--font_weight_regular);
  transition: .2s;
  position: relative;
  border-bottom: 2px solid #0d0d26;
  border-bottom: 2px solid var(--primary_color);
}

.nav-link:hover{
  color: #fff;
  color: var(--white);
  border-bottom: 2px solid #fff;
  border-bottom: 2px solid var(--white);
}

.active{
  border-bottom: 2px solid #0d0d26;
  border-bottom: 2px solid var(--primary_color);
}
.search-container{
  position: absolute;
  z-index: 11;
  right: 0;
}
.search-box {
  transition: width 0.6s, border-radius 0.6s, background 0.6s, box-shadow 0.6s;
  width: 2.01rem;
  height: 2.01rem;
  border-radius: 20px;
  border: 2px solid #0d0d26;
  border: 2px solid var(--primary_color);
  cursor: pointer;
}
.search-box + label .search-icon {
  color:#0d0d26;
  color:var(--primary_color);
  font-size: 1.2rem;
  margin-left: 10%;
}
.search-box:hover {
  color: #0d0d26;
  color: var(--primary_color);
  background: #0d0d26;
  background: var(--primary_color);
}
.search-box:hover + label .search-icon {
  color: white;
}
.search-box:focus {
  transition: width 0.6s cubic-bezier(0, 1.22, 0.66, 1.39), border-radius 0.6s, background 0.6s;
  border: none;
  outline: none;
  box-shadow: none;
  padding-left: 15px;
  cursor: text;
  width: 14rem;
  border-radius: auto;
  background: #ebebeb;
  color: black;
}
.search-box:focus + label .search-icon {
  color: black;
}s

#search-submit {
  position: relative;
  left: -5000px;
}

.search-icon {
  position: relative;
  left: -28px;
  color: white;
  cursor: pointer;
}

.account{
  font-size: 1.9rem;
  color: #0d0d26;
  margin: 0 5% 0 -10%;
  border-radius: 50%;
  border: none;
}
.account:hover{
  cursor: pointer;
}

/* Banner */

.container-home{
  display: flex;
}
.container-home .left{
  width: 75%;
  height: 100vh;
  background-color:#0d0d26;
  background-color:var(--primary_color);
  position: relative;
}
.container-home .right{
  width: 25%;
  height: 100vh;
  background-color: white;
  position: relative;
}

.container-home .container{
  position: absolute;
  color: #fff;
  color: var(--white);
  margin: 10% 10%;
}
.container-home .container .start{
  padding-top: 9%;
}
.container-home .container .end{
  padding-top: 3%;
}
.container-home .container .start h3,h4,p{
    width: 100%;
    padding-top: 2%;
}
.container-home .container .start h3{
  letter-spacing: 2px; 
  font-size: 2.5rem;
  font-weight: 700;
  font-weight: var(--font_weight_bold);
}
.container-home .container .start .btn{
  width: 12rem;
  background-color: #fff;
  background-color: var(--white);
  font-size: 1.1rem;
  font-weight: 500;
  font-weight: var(--font_weight_medium);
  margin-top: 2%;
  padding-left: 5%;
  border-radius: 30px;
}
.container-home .container .start .btn .arrow{
  background-color: #0d0d26;
  background-color: var(--primary_color);
  color: #fff;
  color: var(--white);
  border-radius: 50%;
  height: 2rem;
  width: 2rem;
  margin-left: 15%;
}

/* backtext */
.backtext h1{
  opacity: 0.07;
  font-size: 15rem;
  font-weight: 700;
  font-weight: var(--font_weight_bold);
  z-index: 1;
  margin-left: -2%;
  position: absolute;
}

/* New Arrive */

.new-arrive{
  height: 100%;
  width: 100%;
}
.heading{
  padding: 15% 0 7% 0;
}
.heading h4{
  line-height: 0rem;
  font-size: 2rem;
  /* letter-spacing: 2px; */
}
.heading p{
  line-height: 0.6rem;
  font-size: 1.1rem;
}
.products{
  background-color: #0d0d26;
  background-color: var(--primary_color);
  height: 100%;
}
.products .container{
    padding-bottom: 10%;
}
.products .card{
  height: 8rem;
  border: none;
  margin-bottom: 2%;
  border-radius: 10px;
  padding: 3% 10%;
}
.products .card i{
  font-size: 3rem;
  padding: 1% 0;
}
.products .card:hover{
  cursor: pointer;
}
.new-arrive .card{
  height: 22rem;
  border: none;
  box-shadow: 2px 2px 10px 2px rgba(0, 0, 0, 0.086);
  border-radius: 5%;
  justify-content: center;
  display: flex;
  align-items: center;
  margin-bottom: 4%;
}
.new-arrive .card .card-title{
  line-height: 0;
}
.new-arrive .card .card-text{
  line-height: 1.2rem;
}
.new-arrive .card .card-price{
  color: #0d0d26;
  color: var(--primary_color);
  font-weight: 700;
  font-weight: var(--font_weight_bold);
  font-size: 1.1rem;
  line-height: 1rem;
}
.new-arrive .card img{
  width: 70%;
}
.new-arrive .card .btn{
  width: 10rem;
  background-color: #fff;
  background-color: var(--white);
  color:#000;
  color:var(--black);
  border: 3px solid #0d0d26;
  border: 3px solid var(--primary_color);
  font-weight: 500;
  font-weight: var(--font_weight_medium);
  transition: .4s;
  z-index: 1;
  position: relative;
}
.new-arrive .card .btn::after{
  content: '';
  position: absolute;
  width: 0%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  transition: .4s;
  background-color: #0d0d26;
  background-color: var(--primary_color);
}
.new-arrive .card .btn:hover::after{
  width: 100%;
}
.new-arrive .card .btn:hover{
  color: #fff;
  color: var(--white);
}

/* best offfers */
.best-offer .container-fluid .row {
  height: 20rem;
}
.best-offer .container-fluid img{
  height: 100%;
  width: 100%;
  left: 0;
  position: absolute;
  /* padding-bottom: 4%; */
  /* background-size: 100% 100%; */
}
.best-offer .container-fluid .overlay{
  height: 100%;
  width: 100%;
  position: absolute;
  top:0;
  left:0;
  display: flex;
  background-color: rgba(0,0,0,0.6);
  color: #fff;
  color: var(--white);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.best-offer .container-fluid .overlay h4{
    font-size: 2rem;
}
.best-offer .container-fluid .overlay p{
  width: 40%;
  font-size:1.2rem;
}

/* explore-top */


.explore-top .container-fluid img{
  height: 100%;
  padding: 10% 30% 0% 30%;
  background-size: 100% 100%;
  width: 100%;
  cursor: pointer;
}

/* Footer */

.footer{
  background-color: #0d0d26;
  background-color: var(--primary_color);
  margin-top: 8%;
  padding: 5% 5%;
}
.footer .container h4{
  color: #fff;
  color: var(--white);
  font-size: 2rem;
  font-family: 'Lobster', cursive;
  cursor: pointer;
}
.footer .container i{
  color: #fff;
  color: var(--white);
  font-size: 1.5rem;
  padding-left: 2.5%;
  cursor: pointer;
}
.footer .container p{
  color: #fff;
  color: var(--white);
  font-size: 1.4rem;
}
.footer .container input{
  border-left: none;
  border-right: none;
  border-top: none;
  border-bottom: 1px solid #fff;
  border-bottom: 1px solid var(--white);
  padding: 2% 0;
  background-color: #0d0d26;
  background-color: var(--primary_color);
  width: 20rem;
  color: #fff;
  color: var(--white);
}
.footer .container input:focus{
  color: #fff;
  color: var(--white);
}
.footer .container .btn{
  border: 1.5px solid #fff;
  border: 1.5px solid var(--white);  
  margin-left: 20%;
  color: #fff;
  color: var(--white);
  z-index: 1;
  position: relative;
}

.owl-theme{
  border: none;
  outline: none;
}
.owl-item .item { margin-left: 1px !important; }
.owl-carousel .item .card{
  margin: 0;
  padding: 0;
  height: 22rem;
  width: 16rem;
}
.owl-carousel .owl-prev{
  /* height: 10rem;font-size: 10rem; */
  background-color: #000;
}
:root{
    --primary_color:#0d0d26;
    --white:#fff;
    --black:#000;
    --font_weight_regular:400;
    --font_weight_medium:500;
    --font_weight_semibold:600;
    --font_weight_bold:700;
  }

@media screen and (max-width:600px){
    body{
        overflow-x: hidden;
    }
    .navbar{
        width: 100%;
        padding: 3% 5%;
        background-color: #fff;
        background-color: var(--white);
    }
    .navbar.sticky{
        background-color: #fff;
        background-color: var(--white);
    }
    .navbar-brand{
        color:#0d0d26;
        color:var(--primary_color) ;
        border: none;
    }
    .navbar-nav{
        margin: 0;
        padding-top: 12%;
        text-align: center;
    }
    .navbar-nav .nav-item{
        margin: 0;
    }
    .navbar-nav .nav-link{
        color: #0d0d26;
        color: var(--primary_color);
        border: none;
        line-height: 3rem;
        font-size: 1.1rem;
        font-weight: 500;
        font-weight: var(--font_weight_medium);
        letter-spacing: 2px;
    }
    .navbar-collapse.show{
        height: 25rem;
    }
    .search-container{
        left: 0;
        text-align: center;
    }
    .search-container .search-box {
        background: #fff;
        background: var(--white);
        /* color: var(--primary_color); */
    }
    .search-box + label .search-icon {
        color:#0d0d26;
        color:var(--primary_color);
        font-size: 1rem;
        margin-left: 20%;
    }
    .search-box:focus {
        background-color: #ededed;
    }
    .search-box:focus + label .search-icon {
        color: #000;
        background-color: #ededed;
    }
    .account{
        margin-left: 4%;
    }   
     .navbar span{
        width: 2.4rem;
        height: 2.2rem;
        background-color: white;
        color: #000;
    }
    .navbar-brand{
        font-size: 1.4rem;
    }
    .container-home{
        height: 100%;
        width: 100%;
        margin: 0;
        padding: 0;
    }
    .container-home .left{
        width: 100%;
    }
    .container-home .right{
        display: none;
    }
    .container-home .container{
        margin: 0;
    }
    .container-home .container .start{
        height: 100%;
        padding: 6%;
        position: absolute;
        margin-top: 100%;
    }
    .container-home .container .start h3{
        font-size: 1.4rem;
    }
    .container-home .container .end{
        padding: 30% 5% 3% 5%;
        position: absolute;
        top: 0;
        height: 100%;
    }
    .container-home .container img{
        width: 20rem;
        display: block;
        margin: auto;
    }
    .backtext h1{
        font-size: 6rem;
    }
    .categories .container-fluid{
        padding-top: 20%;
    }
    .heading h4{
        font-size: 1.2rem;
    }
    .heading p{
        font-size: 0.8rem;
        line-height: 1rem;
    }
    .products .card-body{
        padding-top: 12%;
        padding-bottom: 12%;
    }
    .products .card h5{
        font-size: 1rem;
    }
    .products .card h6{
        font-size: 0.8rem;
    }
    .products .card i{
        font-size: 1.5rem;
        padding-top: 12%;
        padding-bottom: 12%;
    }
    .new-arrive .card{
        height: 18rem;
        margin: 0;
        margin-bottom: 10%;
    }
    .new-arrive .card .card-title{
        font-size: 1.1rem;
    }
    .new-arrive .card .card-text{
        font-size: 0.9rem;
    }
    .new-arrive .card .btn{
        width: 7rem;
        font-size: 0.9rem;
    }
    .best-offer .container-fluid .row{
        height: 23rem;
    }
    .best-offer .container-fluid .overlay h4{
        font-size: 1rem;
        line-height: 0.7rem;
    }

    .best-offer .container-fluid .overlay p{
        font-size: 0.7rem;
        width: 90%;
        line-height: 1rem;
    }
    .footer .container h4{
        font-size: 1.4rem;
    }
    .footer .container p{
        font-size: 1rem;
    }
    .footer .container i{
        padding-left: 4%;
    }
    .footer .container input{
        padding: 2% 2%;
        width: 100%;
    }
}
